.main-box-2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.main-box-4 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px;
}

.main-sub-box-4 {
  display: flex;
  width: 90%;
}
.main-sub-box-4 h2 {
  color: var(--primary);
  margin-bottom: 30px;
}

.main-sub-box-4 p {
  margin-bottom: 30px;
}

.main-sub-box-4 a {
  text-decoration: underline;
  color: var(--primary);
}

.makers-col {
  display: flex;
  flex-direction: column;
  padding-right: 11px;
}

.project-col {
  display: flex;
  flex-direction: column;
  padding-right: 11px;
  padding-left: 30px;
}

.divider {
  width: 1px;
  background-color: var(--dark);
  left: 50%;
  height: inherit;
}

.main-quote {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 400px;
  background-color: var(--dark);
}

@media screen and (max-width: 768px) {
  .main-sub-box-4 {
    position: "relative";
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
    width: 100%;
  }
  .main-sub-box-4 h2 {
    color: var(--primary);
    margin-bottom: 30px;
  }

  .main-sub-box-4 p {
    margin-bottom: 30px;
    text-align: justify;
  }

  .project-col {
    padding-left: 0px;
  }

  .divider {
    margin: 20px;
    width: 100%;
    background-color: var(--dark);
    height: 1px;
  }
}
