.button {
    font-weight: 400;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 20px;
}

.button:active {
    background-color: var(--secondary);
    border: none;
    color: var(--light);
}

.button--primary--solid {
    background-color: var(--primary);
    color: var(--white);
    border: none;
}
.button--success--solid {
    background-color: var(--success);
    color: var(--white);
    border: none;
}
.button--warning--solid {
    background-color: var(--warning);
    color: var(--white);
    border: none;
}
.button--white--solid {
    background-color: var(--white);
    color: var(--primary);
    border: none;
}
.button--danger--solid {
    background-color: var(--danger);
    color: var(--white);
    border: none;
}
.button--primary--outline {
    background-color: transparent;
    border: 2px solid var(--primary);
    color: var(--primary);
}
.button--success--outline {
    background-color: transparent;
    border: 2px solid var(--success);
    color: var(--success);
}
.button--warning--outline {
    background-color: transparent;
    border: 2px solid var(--warning);
    color: var(--warning);
}
.button--danger--outline {
    background-color: transparent;
    border: 2px solid var(--danger);
    color: var(--danger);
}

.button--small {
    width: 100px;
    height: 40px;
    font-size: 16px;
}

.button--medium {
    width: 150px;
    height: 50px;
    font-size: 16px;
}

.button--large {
    width: 200px;
    height: 60px;
    font-size: 16px;
}

.button--large--50 {
    width: 50%;
    height: 60px;
    font-size: 16px;
}
.button--large--100 {
    width: 100%;
    height: 60px;
    font-size: 16px;
}

.button2 {
    font-weight: 400;
    cursor: pointer;
}

.button2:hover {
    animation: button2 0.5s ease-in-out;
}

.button2:active {
    background-color: var(--secondary);
    border: none;
    color: var(--light);
}

@keyframes button2 {
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}

.button2-container--medium {
    background-color: var(--white);
    width: 154px;
    height: 42px;
    border: 1px solid var(--dark);
    margin-bottom: 20px;
}

.button2-sub-container--medium {
    background-color: var(--white);
    margin-left: 2px;
    margin-top: -4px;
    width: 154px;
    height: 42px;
    border: 1px solid var(--dark);
}

.button2--medium {
    background-color: var(--white);
    margin-left: 2px;
    margin-top: -4px;
    width: 154px;
    height: 42px;
    border: 1px solid var(--dark);
    font-size: 14px;
}

.button2-container--large {
    background-color: var(--white);
    width: 270px;
    height: 60px;
    border: 1px solid var(--dark);
    margin-bottom: 20px;
}

.button2-sub-container--large {
    background-color: var(--white);
    margin-left: 2px;
    margin-top: -4px;
    width: 270px;
    height: 60px;
    border: 1px solid var(--dark);
}

.button2--large {
    background-color: var(--white);
    margin-left: 2px;
    margin-top: -4px;
    width: 270px;
    height: 60px;
    border: 1px solid var(--dark);
    font-size: 24px;
}
